import React, {useState, useEffect} from 'react';
import CreateCard from './CreateCard';
import AddItem from './AddItem';
import firebase from '../firebase';

function App() {

    //useState for menu 
    const [menu, setMenu] = useState([]);

    //useEffect 
    useEffect(() => {
        //database referance 
        const DbRef = firebase.database().ref("Menu");
        //fetching the items which are present in the database
        DbRef.on('value', (snapshot) => {
            const dbMenu = snapshot.val();
            const menu = [];
            for(let id in dbMenu){
                menu.push({id, ...dbMenu[id]});
            }
            //setting menu array to usestate for map function 
            setMenu(menu);
        }) 
         },[]);
    

    
    return (
        <div>
            {/* calling form component where user will add all the info to be added into database */}
            <AddItem />

            {/* this function will iterate through the menu array */}
            {menu ? menu.map((fItem, index) => {
                return <CreateCard 
                key={index}
                id={fItem.id}
                name={fItem.name} 
                src={fItem.img} 
                details={fItem.details} 
                chef={fItem.chef} 
                rating={fItem.rating} 
                />
            }) : ""}

        </div>
    );

}

export default App;