import React, {useState} from 'react';
import firebase from '../firebase';

function AddItem() {

    const [menuItem, setMenuItem] = useState({
        name: "Hank's Juiciest Chicken Burger",
        img: "https://images.unsplash.com/photo-1568901346375-23c9450c58cd?ixid=MXwxMjA3fDB8MHxzZWFyY2h8MXx8YnVyZ2VyfGVufDB8fDB8&ixlib=rb-1.2.1&w=1000&q=80",
        details: "20m Easy 4 servings",
        chef:"Hank Douglas",
        rating: "4.5",
    });


    function handleChange(event) {
        const {name, value} = event.target;
        setMenuItem((prev)=> {
            return {
                ...prev,
                [name]:value
            };
        });
    }

    function addMenuItem(event) {

        const DbRef = firebase.database().ref('Menu');
        //getting new item from the add item component and putting into database
        DbRef.push(menuItem);
        //preventing default behavoiur of submit button 
        event.preventDefault();
        //👇🏼 After adding a item, inboxs should look like this

        
        setMenuItem({
            name:"",
            details:"",
            chef:"",
            img:"",
            rating:""
        });

        /* if(props.updateId) {
            //fetching the items which are present in the database
            DbRef.on('value', (snapshot) => {
                const dbMenu = snapshot.val();
                //const menu = [];
                for(let id in dbMenu){
                    if(id === props.updateId) {
                        setMenuItem(dbMenu[id]);
                    }
                }
            });
        } */
    }


    return (
        <div className="input-box">
            <input type="text" value={menuItem.name} name="name" onChange={handleChange} placeholder="Medicine"/>
            <input type="text" value={menuItem.chef} name="chef" onChange={handleChange} placeholder="Company"/>
            <input type="text" value={menuItem.details} name="details" onChange={handleChange} placeholder="Details"/>
            <input type="url" value={menuItem.img} name="img" onChange={handleChange} placeholder="Paste the image url"/>
            <input type="number" value={menuItem.rating} name="rating" onChange={handleChange} placeholder="Price"/>
            <button className="input-btn" onClick={addMenuItem}>+</button>
        </div>
    );
}

export default AddItem;