import React from 'react';
import StarRateRoundedIcon from '@material-ui/icons/StarRateRounded';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import firebase from '../firebase';

function CreateCard(props) {

    function deleteItem() {

        //database referance to the perticular id 
        const DbRef = firebase.database().ref("Menu").child(props.id);
        DbRef.remove();
    } 


    return (
        <div className="card">
            <div className="card-content">
                <img className="image" src={props.src} alt={props.name}></img>
                <h1>{props.name}</h1>
                <p>{props.details}</p>
                <p>By <span>{props.chef}</span></p>
                <div className="rating">
                    <StarRateRoundedIcon style={{ color: "green"  }}/>
                    <div className="rating-number" >{props.rating}</div>
                </div>
            </div>
            <div>
            <button className="card-delete-btn" onClick={deleteItem}><DeleteIcon/></button>
            <button className="card-edit-btn"><EditIcon/></button>
            </div>
            
            </div>
            
    );
}

export default CreateCard;